.flex-container1 {
    display: flex;
    background: #fafafa;
}

.flex-child {
    flex: 1;

}  

.flex-child:first-child {
    margin-right: 20px;
    max-width: 100%;
    overflow-x: hidden;
} 

.Heading1 {
    text-align: center;
    font-weight: bold;
    color: #282424;
    margin-top: 100px;
    margin-left: 100px;
    max-width: 100%;
    overflow-x: hidden;
  }

 .paragraph1 {
    text-align: center;
    margin-top: 50px;
    color: #282424;
    margin-left: 100px;
    max-width: 100%;
    overflow-x: hidden;
  }
  
  .Map1 {
    margin-top: 50px;
    margin-right: 100px;
    margin-bottom: 50px;
    float: right;
    max-width: 100%;
    overflow-x: hidden;
  }

  @media only screen and (max-width: 750px){
    .Map1 {
      display: none;
    }

  }