.cards {
    padding: 20rem;
    background: #1C42FF;
    font-weight: normal;

  }
  
  .ServicesHeading {
    text-align: center;
    color: #ffffff;
    font-weight: bold;
    padding: 40px;
  }

  .card__icons{
    padding: 1px;
    color:#282424;
  }

  .cards__title{
    color: #1C42FF;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 200px;
    padding: 15px;
    
  }

  .cards_Cards_TextBody{
    color: #1C42FF;
    text-align: center;
    padding: 20px;
    
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 2000px;
    width: 100%;
    margin: 0 auto;
    padding: 10px;
    font-size: 50;
    
  }

  .cards__container2 {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 2000px;
    width: 100%;
    margin: 0 auto;
    padding: 50px;
    
  }

  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
    
  }
  
  .cards__items {
    margin-bottom: 24px;
    color: #242424;
    
    }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
    
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
    
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
    
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1C42FF;
    box-sizing: border-box;
    
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
    
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #1C42FF;
    font-size: 18px;
    line-height: 24px;
  }

  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
 
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }

  @media only screen and (max-width:600px) {
    .ServicesHeading {
      margin-left: -1.3em;
    }
  }

